import React, { useEffect, useState } from "react";
import ScreenHeading from "../../utilities/ScreenHeading/ScreenHeading";
import ScrollService from "../../utilities/ScrollService";
import Animations from "../../utilities/Animations";
import { Button } from "react-bootstrap";
import "./Resume.css";

const Resume = (props) => {
  /* STATES */
  const [selectedBulletIndex, setSelectedBulletIndex] = useState(0);
  const [carousalOffsetStyle, setCarousalOffsetStyle] = useState({});

  let fadeInScreenHandler = (screen) => {
    if (screen.fadeInScreen !== props.id) return;
    Animations.animations.fadeInScreen(props.id);
  };
  const fadeInSubscription =
    ScrollService.currentScreenFadeIn.subscribe(fadeInScreenHandler);

  /* REUSABLE MINOR COMPONENTS */
  const ResumeHeading = (props) => {
    return (
      <div className="resume-heading">
        <div className="resume-main-heading">
          <div className="heading-bullet"></div>
          <span>{props.heading ? props.heading : ""}</span>
          {props.fromDate && props.toDate ? (
            <div className="heading-date">
              {props.fromDate + "-" + props.toDate}
            </div>
          ) : (
            <div></div>
          )}
        </div>
        <div className="resume-sub-heading">
          <span>{props.subHeading ? props.subHeading : ""}</span>
        </div>
        <div className="resume-heading-description">
          <span>{props.description ? props.description : ""}</span>
          {props.source ? (
            <div>
              <Button
                href={props.source}
                className="link"
                target="_blank"
                variant="secondary"
              >
                Link
              </Button>
            </div>
          ) : (
            <div></div>
          )}
        </div>
      </div>
    );
  };

  /* STATIC RESUME DATA FOR THE LABELS*/
  const resumeBullets = [
    { label: "Education", logoSrc: "education.svg" },
    { label: "Work History", logoSrc: "work-history.svg" },
    { label: "Programming Skills", logoSrc: "programming-skills.svg" },
    { label: "Projects", logoSrc: "projects.svg" },
    { label: "Achievements & Certifications", logoSrc: "work-history.svg" },
    { label: "Familiar With", logoSrc: "interests.svg" },
  ];

  //here we have
  const programmingSkillsDetails = [
    { skill: "Java", ratingPercentage: 95 },
    { skill: "JavaScript", ratingPercentage: 95 },
    { skill: "TypeScript", ratingPercentage: 90 },
    { skill: "Express JS", ratingPercentage: 95 },
    { skill: "React.js", ratingPercentage: 90 },
    { skill: "Node JS", ratingPercentage: 95 },
    { skill: "MongoDB", ratingPercentage: 95 },
    { skill: "HTML", ratingPercentage: 90 },
    { skill: "CSS", ratingPercentage: 90 },
    { skill: "C++", ratingPercentage: 85 },
    { skill: "C", ratingPercentage: 85 },
  ];

  const projectsDetails = [
    {
      title: "MEDIEASY",
      duration: { fromDate: "4/2022", toDate: "4/2022" },
      description:
        "MEDIEASY is a Fully Optimized and Responsive web App that lets you find out nearby hospitals around your location by distance and necessary information at the time of your emergency.",
      subHeading: "Technologies Used: React JS, Bootsrap",
      source: "https://medieasy.netlify.app/",
    },
    {
      title: "Ecommerce Website",
      duration: { fromDate: "10/2021", toDate: "11/2021" },
      description:
        "A React E-commerce Progressive Web App which has Authentication, Database, CRUD, Payment features.",
      subHeading:
        "Technologies Used: MongoDB, Express.js, React.js, Node.js, Bootstrap",
      source: "https://ekart-front.netlify.app/",
    },
  ];

  const resumeDetails = [
    <div className="resume-screen-container" key="education">
      <ResumeHeading
        heading={"Government College of Engineering and Leather Technology"}
        subHeading={"BACHELOR OF TECHNOLOGY IN COMPUTER SCIENCE ENGINEERING"}
        fromDate={"2019"}
        toDate={"2023"}
      />

      <ResumeHeading
        heading={"Bengal Engineering College Model School"}
        subHeading={"SECONDARY AND HIGH SECONDARY"}
        fromDate={"2011"}
        toDate={"2019"}
      />
    </div>,

    /* WORK EXPERIENCE */
    <div className="resume-screen-container" key="work-experience">
      <div className="experience-container">
        <ResumeHeading
          heading={"Techweirdo"}
          subHeading={"Software Engineering Intern"}
          fromDate={"2021"}
          toDate={"2023"}
        />
        <div className="experience-description">
          <span className="resume-description-text">
            - Closely worked with the Nodejs Development Team and made valuable
            contributions to two production-level enterprise applications.{" "}
          </span>
        </div>
        <div className="experience-description">
          <span className="resume-description-text">
            - Design and Develop a Full-Stack internal pay-slip tool with React,
            and Typescript to manage Clients and their Invoices with Edit
            functionality, this allowed us to track who created the invoices and
            when. Adding this feature saved the manual hovering time of the
            company over DB to find these things. On every task related to this,
            the company saved an average of 15 minutes.{" "}
          </span>
          <br />
          <span className="resume-description-text">
            - Create a Node Server for a client-side Project that stores Yolov5
            Object Detection Models.{" "}
          </span>
          <br />
        </div>
      </div>
    </div>,

    /* PROGRAMMING SKILLS */
    <div
      className="resume-screen-container programming-skills-container"
      key="programming-skills"
    >
      {programmingSkillsDetails.map((skill, index) => (
        <div className="skill-parent" key={index}>
          <div className="heading-bullet"></div>
          <span>{skill.skill}</span>
          <div className="skill-percentage">
            <div
              style={{ width: skill.ratingPercentage + "%" }}
              className="active-percentage-bar"
            ></div>
          </div>
        </div>
      ))}
    </div>,

    /* PROJECTS */
    <div className="resume-screen-container" key="projects">
      {projectsDetails.map((projectsDetails, index) => (
        <ResumeHeading
          key={index}
          heading={projectsDetails.title}
          subHeading={projectsDetails.subHeading}
          description={projectsDetails.description}
          fromDate={projectsDetails.duration.fromDate}
          toDate={projectsDetails.duration.toDate}
          source={projectsDetails.source}
        />
      ))}
    </div>,

    /* ACHIEVEMENTS & CERTIFICATIONS*/
    <div className="resume-screen-container" key="achievements">
      <ResumeHeading
        heading="Secured AIR 104 in Googleâ€™s Hash Code in 2022 among 20k+
participants."
      />
      <ResumeHeading
        heading="Secured AIR 193 and a Global Rank of 253 in Credit Suisseâ€™s
Global Coding Challenge in 2022"
      />
      <ResumeHeading
        heading="Secure Top 5th spot in Hack4Bengal Hackathon among 1500
applicants"
      />
      <ResumeHeading
        heading="Solved more than 640+ problems in Leetcode and 400+
problems in GeeksforGeeks"
      />
      <ResumeHeading
        heading="Secured Global rank of 6466, 3423, and 8348 respectively in
Googleâ€™s KickStart in 2021 among 20k+ participants"
      />
      <ResumeHeading heading="Rank 2 in college with a score of 1300+ on GeeksforGeeks" />
    </div>,

    /* Familiar With */
    <div className="resume-screen-container" key="interests">
      <ResumeHeading heading="AWS" />
      <ResumeHeading heading="Nest.js" />
      <ResumeHeading heading="Docker" />
      <ResumeHeading heading="Software Testing" />
      <ResumeHeading heading="Microservices" />
      <ResumeHeading heading="Software Development Life Cycle" />
    </div>,
  ];

  const handleCarousal = (index) => {
    let offsetHeight = 360;
    let newCarousalOffset = {
      style: { transform: "translateY(" + index * offsetHeight * -1 + "px)" },
    };
    setCarousalOffsetStyle(newCarousalOffset);
    setSelectedBulletIndex(index);
  };

  const getBullets = () => {
    return resumeBullets.map((bullet, index) => (
      <div
        onClick={() => handleCarousal(index)}
        className={
          index === selectedBulletIndex ? "bullet selected-bullet" : "bullet"
        }
        key={index}
      >
        <img
          className="bullet-logo"
          src={require(`../../assets/Resume/${bullet.logoSrc}`).default}
          alt="B"
        />
        <span className="bullet-label">{bullet.label}</span>
      </div>
    ));
  };

  const getResumeScreens = () => {
    return (
      <div
        style={carousalOffsetStyle.style}
        className="resume-details-carousal"
      >
        {resumeDetails.map((ResumeDetail) => ResumeDetail)}
      </div>
    );
  };

  useEffect(() => {
    return () => {
      /* UNSUBSCRIBE THE SUBSCRIPTIONS */
      fadeInSubscription.unsubscribe();
    };
  }, [fadeInSubscription]);

  return (
    <div className="resume-container screen-container" id={props.id || ""}>
      <div className="resume-content">
        <ScreenHeading title={"Resume"} subHeading={"My Bio Details"} />
        <div className="resume-card">
          <div className="resume-bullets">
            <div className="bullet-container">
              <div className="bullet-icons"></div>
              <div className="bullets">{getBullets()}</div>
            </div>
          </div>

          <div className="resume-bullet-details">{getResumeScreens()}</div>
        </div>
      </div>
    </div>
  );
};

export default Resume;
